body {
  box-sizing: border-box;
  background-color: #d3d3d3;
  flex-direction: column;
  font-family: Arial, Roboto;
  display: flex;
}

.breed-select {
  width: 40%;
  background-color: #616060;
  margin: 15px;
  padding: 5px;
  display: flex;
}

.breed-select:hover {
  cursor: pointer;
}

.loader, .error {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #ddd;
  margin: 10px 0;
  padding: 10px;
}

.loader {
  border: none;
  display: flex;
}

.load-section {
  flex-direction: row;
  margin: 15px;
  padding: 5px;
  display: flex;
}

.load-spinner {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 15px;
  padding: 5px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader {
  color: #2a2828;
}

.error {
  color: #b71f1f;
}

.cat-info {
  max-width: 70%;
  margin: 20px;
  display: flex;
}

.cat-info > img {
  width: 50%;
  height: auto;
}

